<script>
import CartIndicator from './components/CartIndicator.vue';
import PreloaderCart from './components/PreloaderCart.vue';
import Preloader from './components/Preloader.vue';
import ErrorLoading from './components/ErrorLoading.vue';
import { mapActions, mapMutations } from 'vuex';

export default {
  components: {CartIndicator, Preloader, ErrorLoading, PreloaderCart},
  data() {
        return {
            productsLoading: false,
            productsLoadingFailed: false,
        }
    },
  created() {
    const userAccessKey = localStorage.getItem('userAccessKey');
    if(userAccessKey) {
      this.updateUserAccessKey(userAccessKey);
    }
    this.loadingCart();
    if(!userAccessKey) this.productsLoadingFailed = true///!
  }, 
  methods: {
    ...mapActions(['loadCart']),
    ...mapMutations(['updateUserAccessKey']),
    loadingCart() {
      this.productsLoading = true,
      this.loadCart()
        .then(() => {
          this.productsLoading = false;
          this.productsLoadingFailed = false;
        })
    }
  }

}
</script>

