<template>
    <li class="catalog__item">
        <!-- <a class="catalog__pic" href="#" @click.prevent="gotoPage('product', {id: product.id})">
          <img :src="product.image" :alt="product.title">
        </a> -->

        <router-link class="catalog__pic" :to="{name: 'product', params: {id: product.id}}">
          <img :src="product.image" :alt="product.title">
        </router-link>

        <h3 class="catalog__title">
          <a href="#">
            {{ product.title }}
          </a>
        </h3>

        <span class="catalog__price">
          {{ product.price | numberFormat}} p
        </span>

        <ul class="colors colors--black">
          <li class="colors__item" v-for="color in product.color" :key="Math.random(5000)" >
            <label class="colors__label">
              <input class="colors__radio sr-only" type="radio" :value="color" v-model="colorFirst">
              <span class="colors__value" :style="{'background-color': color}"></span>
            </label>
          </li>
        </ul>
      </li>
</template>

<script>
import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat';

export default {
    data() {
      return {
        colorFirst: this.color
      };
    },
    props: ['product'],
    filters: {
      numberFormat
    },
    methods: {
      gotoPage
    },
    computed: {
      color() {
        return this.product.color ? this.product.color[0] : null
      },
    },
    created(){
        this.colorFirst = this.color
    },
}
</script>